

body {
  margin: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
  background-image: url('./img/Todo.png');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: blue;
}

/* 시간 텍스트 스타일 */
.timeStyle {
  font-size: 7vw;
  font-weight: 600;
  margin: 20px;
}

/* 할 일 아이템 스타일 */
.toDoItemStyle {
  font-size: 1.5vw;
  margin: 10px 10px 0px 10px;
  display: flex;
  align-items: center;
}

/* 체크박스 스타일 */
.checkboxStyle {
  transform: scale(2);
  margin: 0 15px 0 0;
}

/* 삭제 버튼 스타일 */
.deleteButtonStyle {
  background-image: url('./img/trash.png');
  background-size: cover;
  border: none;
  background-color: transparent;
  width: 30px;
  height: 30px;
  margin-left: 15px;
}

/* 할 일 입력창 스타일 */
.toDoInputStyle {
  width: 30vw;
  outline: none;
  border: none;
  border-bottom: 3px solid rgb(58, 19, 121);
  font-size: 2vw;
  background-color: transparent;
  caret-color: black;
  text-align: center;
  color: black;
  margin-bottom: 20px;
}

.completionRate {
  color: black;
  font-size: 1.5vw;
  margin-top: 10px;
}

/* 할 일 입력창 힌트 텍스트 스타일 */
.toDoInputStyle::placeholder {
  color: black;
}

